body {
  margin: 0;
}

li {
  list-style: none;
}

ul, li, p, h2 {
  margin: 0;
  padding: 0;
}

.container {
  margin: 0 auto;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.header__image {
  width: 40px;
  height: 40px;
  display: block;
}

.nav__list {
  display: flex;
}

.nav__item:not(:first-child) {
  margin-left: 20px;
}

.nav__link {
  text-transform: uppercase;
  color: #060b35;
  height: 60px;
  font-size: 12px;
  line-height: 60px;
  text-decoration: none;
  transition: color .3s;
  display: inline-block;
}

.nav__link:hover {
  color: #00acdc;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars--1 :nth-child(-n+1), .stars--2 :nth-child(-n+2), .stars--3 :nth-child(-n+3), .stars--4 :nth-child(-n+4), .stars--5 :nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.stars :not(:last-child) {
  margin-right: 4px;
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
  transition: transform .3s;
}

.card:hover {
  transform: scale(1.2);
}

.card__image {
  text-align: center;
  margin-bottom: 36px;
}

.card__img {
  width: 160px;
  height: 134px;
}

.card__title {
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  transition: color .3s;
  display: inline-block;
}

.card:hover .card__title {
  color: #34568b;
}

.card__code {
  color: #616070;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__stars {
  justify-content: space-between;
  align-items: center;
  margin-block: 16px 24px;
  display: flex;
}

.card__rewiews {
  text-align: end;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__price {
  color: #616070;
  justify-content: space-between;
  margin-bottom: 16px;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
  display: flex;
}

.card__value {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  box-sizing: border-box;
  color: #fff;
  text-transform: uppercase;
  text-align: center;
  transition: color border background-color .3s;
  background-color: #00acdc;
  border: 1px solid #0000;
  border-radius: 5px;
  width: 100%;
  padding-block: 11px;
  font-size: 14px;
  font-weight: 800;
  line-height: 16px;
  display: inline-block;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.main__container {
  grid-template-columns: 200px;
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .main__container {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .main__container {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .main__container {
    grid-template-columns: repeat(4, 200px);
  }
}

html {
  color: #060b35;
  font-family: Roboto, sans-serif;
  font-weight: 500;
}
